@charset "UTF-8";
@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/montserrat/Montserrat-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/montserrat/Montserrat-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/montserrat/Montserrat-Bold.woff2") format("woff2");
}
@-ms-viewport {
  width: device-width;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
}

body {
  min-width: 320px;
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 500;
  font-style: normal;
  margin: 0;
  text-align: left;
  color: #291d0b;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (max-width: 991.9px) {
  body {
    font-size: 16px;
  }
}
@media (max-width: 767.9px) {
  body {
    font-size: 14px;
  }
}

.body-lock {
  position: fixed;
  width: 100%;
  overflow-y: scroll;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  height: 0;
  overflow: visible;
  margin-top: 32px;
  margin-bottom: 32px;
  box-sizing: content-box;
  border: 0;
  border-top: 1px solid #e5e5e5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", Arial, sans-serif;
  line-height: 1.2;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 0.5rem;
  /* stylelint-disable-next-line value-keyword-case */
  color: currentColor;
}

h1 {
  font-size: 60px;
}
@media (max-width: 1359.9px) {
  h1 {
    font-size: 50px;
  }
}
@media (max-width: 991.9px) {
  h1 {
    font-size: 40px;
  }
}
@media (max-width: 767.9px) {
  h1 {
    font-size: 20px;
    text-align: center;
  }
}

h3 {
  font-size: 34px;
  line-height: 1.3;
  color: #291d0b;
}
@media (max-width: 1359.9px) {
  h3 {
    font-size: 30px;
  }
}
@media (max-width: 767.9px) {
  h3 {
    font-size: 18px;
  }
}

p,
ul,
ol,
dl,
table,
blockquote,
pre,
address,
figure {
  margin-top: 0;
  margin-bottom: 10px;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

li {
  line-height: inherit;
}

address {
  font-style: normal;
}

blockquote {
  margin-right: 0;
  margin-left: 0;
  padding: 0;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 14px;
}

sub,
sup {
  font-size: 14px;
}

a,
button,
[role=button],
input,
label,
select,
textarea {
  touch-action: manipulation;
  margin: 0;
}

a {
  text-decoration: none;
  color: #0000ff;
  background-color: transparent;
  outline: none;
  transition: color 0.3s;
}
a:hover, a:focus {
  color: #0000b3;
}

a:not([href]) {
  text-decoration: none;
  color: inherit;
}
a:not([href]):hover, a:not([href]):focus {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
  border-style: none;
}

svg {
  /* stylelint-disable-next-line value-keyword-case */
  fill: currentColor;
  /* stylelint-disable-next-line value-keyword-case */
  color: currentColor;
}
a svg, button svg {
  pointer-events: none;
}
svg:not(:root) {
  overflow: hidden;
}

table {
  border-collapse: collapse;
}

[role=button] {
  cursor: pointer;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=date],
input[type=time],
input[type=datetime-local],
input[type=month] {
  -webkit-appearance: listbox;
}

input[type=radio],
input[type=checkbox] {
  padding: 0;
  box-sizing: border-box;
}

[hidden] {
  display: none !important;
}

textarea {
  overflow: auto;
  resize: vertical;
}

