.about {
  min-height: 1040px;
  background-image: url("../img/about-bg.jpg");
  background-repeat: no-repeat;
  background-position: top center;
}
.about .about__btn {
  margin: 0;
  margin-top: 20px;
  box-shadow: none;
}
.about__inner {
  width: 100%;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}
