.about__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}
.about__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(100% - 20px);
}
@media screen and (min-width: 768px) {
  .about__item {
    width: calc(58.3333333333% - 20px);
  }
}
.about__item .content__title:after {
  background-image: url(../img/about.png);
}
.about__info {
  line-height: 28px;
