.btn {
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  font-family: inherit;
  font-size: 18px;
  line-height: 1;
  font-weight: 400;
  margin: 0;
  padding: 16px 40px;
  box-sizing: border-box;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  text-transform: none;
  text-overflow: ellipsis;
  color: #007134;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #007134;
  border-radius: 26px;
  transition: background-color 0.3s;
  cursor: pointer;
  user-select: none;
}
@media (max-width: 1359.9px) {
  .btn {
    padding: 10px 30px;
    border-radius: 19px;
  }
}
@media (max-width: 991.9px) {
