    font-size: 16px;
  }
}
@media (max-width: 767.9px) {
  .btn {
    font-size: 14px;
  }
}
.btn:hover, .btn:focus {
  text-decoration: none;
  color: #ffffff;
  background-color: #5cac3e;
  border-color: #5cac3e;
}
.btn--disabled, .btn:disabled {
  opacity: 0.7;
  color: #291d0b;
  background-color: #e6e6e6;
  cursor: not-allowed;
}
a.btn--disabled {
  pointer-events: none;
}

.btn--green {
  color: #ffffff;
  background-color: #76c35a;
  border: 1px solid #76c35a;
  box-shadow: 0 0 5px 5px rgba(118, 195, 90, 0.39);
}
@media (max-width: 767.9px) {
