    margin-right: 10px;
    margin-bottom: 20px;
    margin-left: 10px;
  }
}
.btn--brown {
  margin-right: 20px;
  color: #ffffff;
  background-color: rgba(22, 12, 11, 0.722);
  border: 1px solid rgba(22, 12, 11, 0.722);
}
@media (max-width: 767.9px) {
