// font-weight helper:

// 100 Extra Light or Ultra Light;
// 200 Light or Thin;
// 300 Book or Demi;
// 400 Regular or Normal;
// 500 Medium;
// 600 Semibold or Demibold;
// 700 Bold;
// 800 Black or Extra Bold or Heavy;
// 900 Extra Black or Fat or Ultra Black

@mixin font($f-name, $f-style, $f-weight, $f-url) {
  @font-face {
    font-family: $f-name;
    font-weight: $f-weight;
    font-style: $f-style;
    src: url($f-url) format('woff2');
  }
}

@include font('Montserrat', normal, 400, '../fonts/montserrat/Montserrat-Regular.woff2');

@include font('Montserrat', normal, 500, '../fonts/montserrat/Montserrat-Medium.woff2');

@include font('Montserrat', normal, 700, '../fonts/montserrat/Montserrat-Bold.woff2');

